<template>
    <div class="layout">
        <div class="layout__content">
            <div class="main" :class="{'booking-fullscreen' : fullscreenModeOn}">
                <sidebar v-if="isIn"></sidebar>
                <div class="content" :class="{'wide': sidebarMinimized}">
                    <slot></slot>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import Sidebar from "../global/Sidebar";

export default {
    name: "layout",
    components: {
        Sidebar
    },
    data() {
        return {

        }
    },
    computed: {
        isIn() {
            return this.$route.name !== 'Login'
        },
        ...mapState(['user', 'sidebarMinimized', 'fullscreenModeOn']),
        ...mapGetters(['isLoggedIn'])
    },
    methods: {
        logout() {
            this.$store.dispatch('logout');
        }
    }
}
</script>