<template>
    <div class="section booking">
        <div class="booking__wrap">
            <div class="booking__left">
                <div class="site-input">
                    <label for="tables">Зал</label>
                    <div class="input-item input-item_booking">
                        <multiselect
                                id="tables"
                                v-model="selectedSpace"
                                :options="restaurantSpaces"
                                :searchable="false"
                                :close-on-select="true"
                                placeholder="Выберите столы"
                                :multiple="false"
                                label="name"
                                track-by="id"
                                :selectLabel="''"
                                :deselectLabel="''"
                                :selectedLabel="'•'"
                        ></multiselect>
                    </div>
                </div>


                <div class="site-input">
                    <label for="date_time">Дата</label>
                    <div class="input-item">
                        <datepicker
                                id="date_time"
                                v-model="date"
                                lang="ru"
                                appendToBody=""
                                format="YYYY-MM-DD"
                                :first-day-of-week="1"
                                :width="'auto'"
                                :valueType="'format'"
                                :editable="false"
                                :clearable="false"
                                @change="getBooking"
                        ></datepicker>
                    </div>
                </div>
                <div class="booking__comment" v-if="loaded">
                    <div v-if="!editComment" class="booking__comment-text">
                        <span v-if="booking.tablesForDate.day_description">{{booking.tablesForDate.day_description}}</span>
                        <span v-if="!booking.tablesForDate.day_description">[описание дня будет здесь]</span>
                        <span @click="tryEditComment"><i class="fas fa-pencil-alt"></i></span>
                    </div>
                    <div v-if="editComment">
                        <textarea v-model="booking.tablesForDate.day_description"></textarea>
                        <p class="btn btn_success" @click="editTablePresetForDate({goToForm: false})">Сохранить</p>
                    </div>
                </div>
            </div>

            <div class="booking__center">
                <h4 class="booking__event-subtitle">События в этот день:</h4>
                <div v-if="booking.events && booking.events.length">
                    <div class="booking__event" v-for="(event, index) in booking.events" :key="index">
                        <div class="booking__event-img" v-if="event.image_url">
                            <img :src="event.image_url" alt/>
                        </div>
                        <div class="booking__event-content">
                            <div class="booking__event-title">
                                <h3>{{event.title}}</h3>
                            </div>
                            <div class="booking__event-params">
                                <h4>
                                    Цена билета:
                                    <span>{{event.ticket_price}}</span>
                                </h4>
                                <h4>
                                    Минимальный депозит:
                                    <span>{{event.min_deposit}}</span>
                                </h4>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="book" v-if="loaded" :class="{'client': clientMode}">
            <div class="preset preset_nm" ref="preset"
                 :style="{
                            'background': 'url(' + selectedSpace.image_url + ') center center / 100% 100% no-repeat',
                            'width': selectedSpace['size_x'] + 'px',
                            'height': selectedSpace['size_y'] + 'px',
                         }"
            >
                <div class="book__controls">
                    <div class="switch-tables-mode">
                        <label for="switch">Режим гостя</label>
                        <input type="checkbox" id="switch" v-model="clientMode">
                    </div>
                    <div class="fullscreen-mode" @click="toggleFullscreen">
                        <span v-if="fullscreenModeOn">
                            В стандартный режим
                            <i class="fas fa-compress"></i>
                        </span>
                        <span v-else>
                            На весь экран
                            <i class="fas fa-expand"></i>
                        </span>
                    </div>
                </div>
                <div v-for="(table, index) in tablePresetObj['tables']"
                     :key="index"
                     class="preset__table"
                     :data-background="table.image_url"
                     :class="{
                        'selected': showTableMenu === table.id,
                        'additional': table.additional,
                        'busy': todayIsTheDay && table.busy_now === 'busy',
                        'draft': table.busy_now === 'draft',
                        'booked': table.reserves.length > 0,
                        'expired': todayIsTheDay && isExpired(table),
                        'group': isInActiveGroup(table.group)
                     }"
                     :style="{
                                'top': table['pivot']['coord_y'] + 'px',
                                'left': table['pivot']['coord_x'] + 'px',
                                'height': table.seats_count * tableScale + 'px',
                                'background-image': 'none',
                                'width': table.seats_count * tableScale + 'px',
                                'margin-top': -table.seats_count * tableScale / 2 + 'px',
                                'margin-left': -table.seats_count * tableScale / 2 + 'px',
                                'box-shadow': '0 0 4px 4px ' + getGroupColor(table.group)
                            }"
                     @click="table.reserves.length   ? openTableMenu(table)  : addReserve(table)"
                >
                    <div class="preset__table-reserve-options" v-if="table.reserves.length">
                        <span v-for="(option, index) in getOptionsArr(table.reserves[0].options)" :key="index" class="option" :class="option.class">{{option.value}}</span>
                    </div>
                    <div class="table-content">
                        <div class="text-wrapper">
                            <div class="text">
                                <div class="name">{{table.name}}</div>
                                <div class="seats">(Мест: {{table.seats_count}})</div>
                                <div class="preset__table-reserve" v-for="(reserve, index) in table.reserves" :key="index"
                                     :style="{
                                        'height': 100 / table.reserves.length + '%',
                                        'width': '80%',
                                     }"
                                >
                                    <div class="preset__table-reserve-text">
                                        <span>{{reserve.persons_count}} чел. </span>
                                        <span>{{moment(reserve.date_start).format('HH:mm')}} - {{moment(reserve.date_finish).format('HH:mm')}} </span>
                                        <span>{{reserve.client.name}} {{reserve.client.last_name || ''}}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="booking-menu-wrapper" v-show="showTableMenu === tableToShow.id" @click.stop.self="showTableMenu = null">
                    <div class="booking-menu" @click.stop.self="showTableMenu = null">
                        <div class="booking-menu__body">
                            <h4>Стол: {{tableToShow.name}}</h4>
                            <h6>(на {{tableToShow.seats_count}} человек)</h6>
                            <button class="btn btn_pale" @click="takeTable(tableToShow)" v-if="!tableToShow.busy_now && todayIsTheDay">Занять стол</button>
                            <button class="btn btn_pale" @click="freeTable(tableToShow)" v-if="tableToShow.busy_now === 'busy' && todayIsTheDay">Освободить стол</button>
                            <button class="btn btn_pale" :disabled="true" v-if="tableToShow.busy_now === 'draft'">Стол в процессе букинга</button>
                            <p class="menu-section"><b>Резервы на сегодня:</b></p>
                            <div class="reserve" v-for="(reserve, index) in tableToShow.reserves" :key="index">
                                <p>{{reserve.client.name}} {{reserve.client.last_name || ''}}</p>
                                <p>{{reserve.client.phone}}</p>
                                <p>{{reserve.persons_count}} чел.</p>
                                <p>Статус: {{reserve.status}}</p>
                                <p v-if="reserve.prepayment">Предоплата: {{reserve.prepayment}}</p>
                                <p>С: {{reserve.date_start.slice(10, reserve.date_start.length - 3)}}</p>
                                <p>До: {{reserve.date_finish.slice(10, reserve.date_finish.length - 3)}}</p>
                                <p>Комментарий: {{reserve.comment}}</p>
                                <button class="btn btn_primary" :disabled="!!tableToShow.busy_now && todayIsTheDay" @click="editReserve(reserve)">Редактировать резерв</button>
                                <button class="btn btn_danger" :disabled="!!tableToShow.busy_now && todayIsTheDay" @click="deleteReserve(reserve)">Удалить резерв</button>
                            </div>
                            <p v-if="tableToShow.reserves.length === 0">[нет резервов]</p>
                            <button class="btn btn_success" :disabled="!!tableToShow.busy_now && todayIsTheDay" @click="addReserve(tableToShow)">Добавить резерв</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="booking__bottom" v-if="loaded">
            <div class="booking__bottom-buttons">
                <button class="btn btn_primary" @click="bookAllTables">Занять весь зал</button>
                <button class="btn btn_success" @click="editTablePresetForDate({goToForm: true})">Изменить расстановку</button>
            </div>
            <div class="booking__overbookReserves">
                <div class="overbooking-container">
                    <div v-for="(reserve, index) in overbookReserves"
                         :key="index"
                         class="preset__table booked"
                         :class="{
                            'selected': showTableMenu === reserve.id,
                            'additional': reserve.additional,
                            'expired': todayIsTheDay && isExpired(reserve),
                            'group' :isInActiveGroup(reserve.group)
                         }"
                         :style="{

                                    'background-image': 'none',
                                    'box-shadow': '0 0 4px 4px ' + getGroupColor(reserve.group)
                                }"
                         @click="editReserve(reserve)"
                    >
                        <div class="preset__table-reserve-options" v-if="reserve.length">
                            <span v-for="(option, index) in getOptionsArr(reserve[0].options)" :key="index" class="option" :class="option.class">{{option.value}}</span>
                        </div>
                        <div class="table-content">
                            <div class="text-wrapper">
                                <div class="text">

                                    <div class="preset__table-reserve"  :key="index"
                                         :style="{
                                            'height': 100 / reserve.length + '%',
                                            'width': '80%',
                                         }"
                                    >
                                        <div class="preset__table-reserve-text">
                                            <span>{{reserve.persons_count}} чел. </span>
                                            <span>{{moment(reserve.date_start).format('HH:mm')}} - {{moment(reserve.date_finish).format('HH:mm')}} </span>
                                            <span>{{reserve.client.name}} {{reserve.client.last_name || ''}}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="reserveToDelete" class="modal" :class="{'fade': !showReserveDeleteConfirmation, 'open' : showReserveDeleteConfirmation}"
             @click.self.stop="showReserveDeleteConfirmation = false">
            <div class="modal-dialog modal-md">
                <div class="modal-content">
                    <div class="modal-body">
                        <div class="popup__close" @click="showReserveDeleteConfirmation = false">&times;</div>
                        <h4>Удалить резерв {{reserveToDelete.id}}?</h4>
                        <div class="popup__buttons popup__buttons_spread popup__buttons_mt">
                            <button class="btn btn_primary" @click.prevent="showReserveDeleteConfirmation = false">Отмена</button>
                            <button class="btn btn_danger" @click.prevent="deleteConfirmed(reserveToDelete)">Удалить</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Multiselect from "vue-multiselect";
import Datepicker from "vue2-datepicker";
import {apiUrls, misc} from "@/config/constants";
import handleFormValidationErrors from "@/assets/js/mixins/handleFormValidationErrors";
import moment from 'moment';
import {mapState} from 'vuex';

const groupColors = [
    '#FF6633', '#FF33FF', '#00B3E6',
    '#3366E6', '#999966', '#99FF99', '#B34D4D',
    '#CCCC00', '#66E64D', '#4D80CC', '#9900B3',
    '#E64D66'
];

export default {
    name: "Booking",
    components: {
        Multiselect,
        Datepicker
    },
    mixins: [handleFormValidationErrors],
    data() {
        return {
            moment: moment,
            booking: {tablesForDate: {}, tablePreset: {}, events: [], reserves: [] },
            loaded: false,
            loading: false,
            date: '',
            selectedSpace: {},
            restaurantSpaces: [],
            comment: '',
            editComment: false,
            tablePresetObj: {tablesForDate: {}, tablePreset: {}},
            showTableList: false,
            additionalTable: null,
            showTableMenu: null,
            tableToShow: {reserves: []},
            reserveToEdit: null,
            reserveToDelete: null,
            showReserveDeleteConfirmation: false,
            reserveToAdd: null,
            clientMode: false,
            overbookReserves: [],
            changePreset: true,
            todayIsTheDay: true,
            bookingInterval: null
        };
    },
    computed: {
        tableScale() {
            return this.tablePresetObj && this.tablePresetObj.scale ? this.tablePresetObj.scale * 5 : 5;
        },
        ...mapState(['fullscreenModeOn'])
    },
    watch: {
        clientMode(val) {
            $('.preset__table').each((index, el) => {
                $(el).css({
                    'background-image': val ? 'url(' + $(el).data('background') : 'none',
                    'background-size': '100% 100%',
                    'background-repeat': 'no-repeat',
                    'background-position': 'center center'
                });
            })
        },
        selectedSpace() {
            this.getBooking();
        }
    },
    methods: {
        getRestaurantSpaces() {
            return this.$store.dispatch('getRestaurantSpaces')
                .then(response => {
                    this.selectedSpace = response[0];
                    this.restaurantSpaces = response;
                })
                .catch(error => console.log(error));
        },
        getBooking() {
            if (this.loading || !this.date) return;

            this.loading = true;

            return this.$http.get(apiUrls.booking + '/' + this.selectedSpace.id + '/' + this.date)
                .then(response => {
                    this.booking = response.data.data;
                    this.tablePresetObj = this.booking['tablePreset'];
                    this.overbookReserves = this.booking.overbookReserves;
                    if (!this.booking.tablesForDate) {
                        this.booking.tablesForDate = {additional_tables: []};
                    }

                    this.tablePresetObj.tables = this.tablePresetObj.tables.concat(this.booking.tablesForDate.additional_tables).map(table => Object.assign(table, {reserves: []}));

                    this.booking.reserves.forEach((reserve, index) => {
                        reserve.tables.forEach(reserveTable => {
                            let tableToUpdate = this.tablePresetObj.tables.find(table => table.id === reserveTable.id);
                            tableToUpdate.reserves = tableToUpdate.reserves ? tableToUpdate.reserves : [];
                            tableToUpdate.reserves.push(reserve);
                            if (reserve.tables.length > 1) {
                                tableToUpdate.group = {index: index, start: reserve.date_start, finish: reserve.date_finish};
                            }
                        })
                    });

                    this.loaded = true;
                    this.loading = false;

                    let today = new Date(this.dateSubtractHours(misc.WORKING_HOURS_OFFSET)),
                        theDay = new Date(this.date);
                    this.todayIsTheDay =
                        theDay.getDate() === today.getDate() &&
                        theDay.getMonth() === today.getMonth() &&
                        theDay.getFullYear() === today.getFullYear();
                })
                .catch(error => {
                    this.handleFormValidationErrors(error, this.$refs.form);
                });
        },
        tryEditComment() {
            this.editComment = true;
        },
        openTableMenu(table) {
            this.tableToShow = table;
            this.showTableMenu = table.id;
        },
        editReserve(reserve) {
            reserve.table_id = this.showTableMenu;
            this.startDraft(this.tableToShow);
            this.showTableMenu = null;
            this.$store.state.bookingState = {
                space: this.selectedSpace,
                date: this.date,
                table: reserve.tables
            };
            this.$router.push('/reserves/' + reserve.id + '/edit');
        },
        addReserve(table) {
            this.startDraft(table);
            this.$store.state.bookingState = {
                space: this.selectedSpace,
                date: this.date,
                table: table
            };
            this.$router.push('/reserves/create');
        },
        deleteReserve(reserve) {
            this.reserveToDelete = reserve;
            this.showReserveDeleteConfirmation = true;
        },
        deleteConfirmed(reserve) {
            this.$http.delete(apiUrls.reserve + '/' + reserve.id)
                .then(resp => {
                    this.showReserveDeleteConfirmation = false;

                    if (resp.data && resp.data.success === true) {
                        this.getBooking().then(() => {
                            this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                        });
                    }
                })
                .catch(err => console.log(err));
        },
        takeTable(tableEntity, update = false) {
            if (!tableEntity) return;

            if ('number' === typeof tableEntity.id) {
                tableEntity.busy_now = 'busy';
                this.$http.post('tables/' + tableEntity.id, tableEntity)
                    .then(() => {
                        if (update) {
                            this.getBooking().then(() => {
                                this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                            });
                        }
                    })
            } else if (tableEntity.length) {
                tableEntity[0].busy_now = 'busy';
                this.$http.post('tables/' + tableEntity[0].id, tableEntity[0])
                    .then(() => {
                        if (update) {
                            this.getBooking().then(() => {
                                this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                            });
                        }
                    })
            }
        },
        startDraft(tableEntity, update = false) {
            if (!tableEntity) return;

            if ('number' === typeof tableEntity.id) {
                if (tableEntity.busy_now === 'busy') return;
                tableEntity.busy_now = 'draft';
                this.$http.post('tables/' + tableEntity.id, tableEntity)
                    .then(() => {
                        if (update) {
                            this.getBooking().then(() => {
                                this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                            });
                        }
                    })
            } else if (tableEntity.length) {
                if (tableEntity[0].busy_now === 'busy') return;
                tableEntity[0].busy_now = 'draft';
                this.$http.post('tables/' + tableEntity[0].id, tableEntity[0])
                    .then(() => {
                        if (update) {
                            this.getBooking().then(() => {
                                this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                            });
                        }
                    })
            }
        },
        freeTable(tableEntity, update = false) {
            return new Promise(resolve => {
                if (!tableEntity) resolve();

                if ('number' === typeof tableEntity.id) {
                    tableEntity.busy_now = '';
                    this.$http.post('tables/' + tableEntity.id, tableEntity)
                        .then(() => {
                            if (update) {
                                this.getBooking().then(() => {
                                    this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                                    resolve();
                                });
                            } else {
                                resolve();
                            }
                        })
                } else if (tableEntity.length) {
                    tableEntity[0].busy_now = '';
                    this.$http.post('tables/' + tableEntity[0].id, tableEntity[0])
                        .then(() => {
                            if (update) {
                                this.getBooking().then(() => {
                                    this.tableToShow = this.tablePresetObj.tables.find(table => table.id === this.tableToShow.id);
                                    resolve();
                                });
                            } else {
                                resolve();
                            }
                        })
                }
            })

        },
        getOptionsArr(reserveOptions) {
            if (reserveOptions && reserveOptions.length) {
                return reserveOptions.split(', ').map(option => {
                    let formattedOption = {};
                    switch (option) {
                        case 'ПО':
                            formattedOption.class = 'po';
                            break;
                        case 'ПС':
                            formattedOption.class = 'ps';
                            break;
                        case 'ПЗ':
                            formattedOption.class = 'pz';
                            break;
                        default:
                            break;
                    }

                    formattedOption.value = option;

                    return formattedOption
                })
            } else {
                return []
            }
        },
        isExpired(table) {
            return table.reserves && table.reserves.length && new Date().getTime() > new Date(table.reserves[0].date_start).getTime()
        },
        isInActiveGroup(group) {
            return group ? moment(new Date()).isBetween(moment(group.start), moment(group.finish)) : false
        },
        getGroupColor(group) {
            if (!group || !group.hasOwnProperty('index')) return 'transparent';
            return 'number' === typeof group.index ? groupColors[group.index] : 'transparent';
        },
        editTablePresetForDate({goToForm = false}) {
            this.editComment = false;
            if (this.booking.tablesForDate.id) {
                this.$http.put(apiUrls.tablesForDate + '/' + this.booking.tablesForDate.id, {
                    day_description: this.booking.tablesForDate.day_description,
                    date: this.booking.tablesForDate.date,
                    table_preset_id: this.booking.tablesForDate.table_preset_id,
                    restaurant_space_id: this.booking.tablesForDate.restaurant_space_id
                })
                    .then(() => {
                        this.getBooking();
                        if (goToForm) {
                            this.$store.dispatch('setItemToEdit', {id: this.booking.tablesForDate.id, type: 'tablesForDate'})
                                .then(() => {
                                    this.$router.push('/table-presets/on-date');
                                });
                        }
                    })
            } else {
                this.$http.post(apiUrls.tablesForDate, {
                    day_description: this.booking.tablePreset.day_description,
                    table_preset_id: this.booking.tablePreset.id,
                    date: this.date,
                    restaurant_space_id: this.booking.tablePreset.restaurant_space_id
                })
                    .then(() => {
                        this.getBooking();
                        if (goToForm) {
                            this.$store.dispatch('setItemToEdit', {id: 'last', type: 'tablesForDate'})
                                .then(() => {
                                    this.$router.push('/table-presets/on-date');
                                });
                        }
                    })
            }
        },
        toggleFullscreen() {
            this.$store.dispatch('toggleBookingFullscreen');
        },
        bookAllTables() {
            this.$store.state.bookingState = {
                space: this.selectedSpace,
                date: this.date,
                table: this.tablePresetObj.tables
            };
            this.$router.push('/reserves/create');
        },
        dateSubtractHours(hours) {
            return new Date().setTime(new Date().getTime() - hours * 60 * 60 * 1000);
        },
    },
    mounted() {
        this.getRestaurantSpaces().then(() => {
            if (this.$store.state.bookingState) {
                if (this.$store.state.bookingState.selectedSpace) {
                    this.selectedSpace = Object.assign({}, this.$store.state.bookingState.selectedSpace);
                }
                this.date = this.$store.state.bookingState.date;

                if (this.$store.state.bookingState.table && this.$store.state.bookingState.table.busy_now === 'draft') {
                    this.freeTable(this.$store.state.bookingState.table)
                        .then(() => {
                            this.getBooking();
                        });
                } else {
                    this.getBooking();
                }
            } else {
                this.date = moment(new Date(this.dateSubtractHours(misc.WORKING_HOURS_OFFSET))).format('YYYY-MM-DD');
                this.getBooking();
            }
            this.$store.state.bookingState = null;
        });

        this.bookingInterval = setInterval(this.getBooking, 60000);
    },
    beforeDestroy() {
        clearInterval(this.bookingInterval);
    }
};
</script>