<template>
    <div>
        <h2 class="section__title" v-if="type === 'create'">Создать расстановку столов</h2>
        <h2 class="modal__title" v-if="type === 'edit'">Редактировать расстановку столов на {{tablePresetForDateObj.date ? tablePresetForDateObj.date.slice(0, 10) : ''}}</h2>
        <div>
            <div class="pre-form">
                <p>На базе расстановки: <b>{{tablePresetForDateObj.table_preset.name}}</b> |
                    {{tablePresetForDateObj.table_preset.comment}}</p>
                <p>Зал: {{tablePresetForDateObj.restaurant_space_id}}</p>
            </div>
            <form class="form" @submit.prevent="submitItem" ref="form">
                <div class="site-input">
                    <label for="date_time">Дата</label>
                    <div class="input-item">
                        <datepicker
                            id="date_time"
                            v-model="tablePresetForDateObj.date"
                            lang="ru"
                            :appendToBody="false"
                            format="YYYY-MM-DD"
                            :first-day-of-week="1"
                            :width="'auto'"
                            :editable="false"
                            :valueType="'format'"
                        ></datepicker>
                    </div>
                </div>
                <div class="site-input">
                    <label for="table_preset_id">Выбрать другой шаблон расстановки</label>
                    <div class="input-item" id="table_preset_id">
                        <multiselect v-model="tablePresetForDateObj.table_preset"
                                     :options="tablePresets"
                                     :close-on-select="true"
                                     :clear-on-select="true"
                                     placeholder="Выберите расстановку"
                                     :multiple="false"
                                     :trackBy="'id'"
                                     :label="'name'"
                                     :selectLabel="'Выбрать'"
                                     :deselectLabel="'Убрать'"
                                     :selectedLabel="'Выбрано'"
                        >
                            <span slot="noResult">По вашему запросу нет результатов.</span>
                        </multiselect>
                    </div>
                </div>
                <div class="site-input">
                    <label for="day_description">Описание дня</label>
                    <div class="input-item">
                        <input id="day_description" type="text" v-model="tablePresetForDateObj.day_description">
                    </div>
                </div>

                <div v-if="tablePresetForDateObj.table_preset['restaurant_space']">
                    <h4>Расстановка:</h4>
                    <h5>(Масштаб столов: {{tablePresetForDateObj.table_preset.scale}})</h5>
                    <div class="moving-message" v-show="movingTable">
                        Кликните по нужному месту на схеме для перемещения туда стола или введите точные координаты.
                        <button class="btn btn_danger" @click.stop.prevent="cancelTableMove">Выйти из режима перемещения стола</button>
                    </div>
                    <div class="preset" ref="preset" :class="{'moving': movingTable}"
                         :style="{
                                'background': 'url(' + tablePresetForDateObj.table_preset['restaurant_space'].image_url + ') center center / 100% 100% no-repeat',
                                'width': tablePresetForDateObj.table_preset['restaurant_space']['size_x'] + 'px',
                                'height': tablePresetForDateObj.table_preset['restaurant_space']['size_y'] + 'px',
                             }"
                         @click.self.prevent="openPresetMenu"
                    >
                        <div class="preset__click-zone" ref="clickZone"></div>
                        <div v-for="(table, index) in tablePresetForDateObj.table_preset['tables']"
                             :key="index"
                             class="preset__table"
                             :class="{
                                'selected': showTableMenu === table.id
                             }"
                             :style="{
                                    'top': table['pivot']['coord_y'] + 'px',
                                    'left': table['pivot']['coord_x'] + 'px',
                                    'background': 'url(' + table.image_url + ') center center / 100% 100% no-repeat',
                                    'height': table.seats_count * tableScale + 'px',
                                    'min-height': '30px',
                                    'width': table.seats_count * tableScale + 'px',
                                    'min-width': '30px',
                                    'margin-top': getMargin(table.seats_count),
                                    'margin-left': getMargin(table.seats_count)
                                }"
                        >
                            <span class="name">{{table.name}}</span>
                        </div>
                        <div v-for="(table, index) in tablePresetForDateObj['additional_tables']"
                             :key="'a' + index"
                             class="preset__table additional"
                             :class="{
                                'selected': showTableMenu === table.id,
                                'movable': tableToMove.id === table.id
                             }"
                             :style="{
                                    'top': table['pivot']['coord_y'] + 'px',
                                    'left': table['pivot']['coord_x'] + 'px',
                                    'background': 'url(' + table.image_url + ') center center / 100% 100% no-repeat',
                                    'height': table.seats_count * tableScale + 'px',
                                    'width': table.seats_count * tableScale + 'px',
                                    'margin-top': getMargin(table.seats_count),
                                    'margin-left': getMargin(table.seats_count)
                                }"
                             @click.stop.prevent="openTableMenu($event, table)"

                        >
                            <span class="name">{{table.name}}</span>
                            <div class="menu" v-show="showTableMenu === table.id"
                                 :style="{
                                     'top': tableMenuY + 'px',
                                     'left': tableMenuX + 'px'
                                 }"
                            >
                                <span class="menu__option" @click.stop="openMoveTable(table)">Переместить стол</span>
                                <span class="menu__option" @click.stop="openDeleteTable(table)">Удалить стол</span>
                            </div>

                            <div class="coordinates" v-if="movingTable && tableToMove.id === table.id">
                                <div class="coordinates__row">
                                    <label for="move-x">X:</label>
                                    <input id="move-x" type="number" v-model="tableToMove.pivot.coord_x">
                                </div>
                                <div class="coordinates__row">
                                    <label for="move-y">Y:</label>
                                    <input id="move-y" type="number" v-model="tableToMove.pivot.coord_y">
                                </div>
                                <div class="coordinates__save" @click="confirmTableMove"><span>&#10004;</span></div>
                            </div>
                        </div>
                        <div class="menu menu_btlr" v-show="showPresetMenu"
                             :style="{
                                     'top': presetMenuY + 'px',
                                     'left': presetMenuX + 'px',
                                 }"
                        >
                            <span class="menu__option" @click="openAddTable">Добавить стол</span>
                        </div>
                        <div class="preset__delete-table" v-if="deletingTable">
                            <h4>Удалить стол {{tableToDelete.name}} ?</h4>
                            <span class="close" @click="deletingTable = false">&times;</span>
                            <button class="btn btn_danger" @click="confirmDeleteTable">Удалить</button>
                        </div>
                        <div class="preset__add-table" v-if="showTableList">
                            <h4>Добавить стол</h4>
                            <span class="close" @click="showTableList = false">&times;</span>
                            <div class="tables-dropdown">
                                <div class="site-input">
                                    <div class="input-item" id="status">
                                        <multiselect v-model="additionalTable"
                                                     :options="tables"
                                                     :searchable="true"
                                                     :close-on-select="true"
                                                     :clear-on-select="true"
                                                     placeholder="Выберите стол"
                                                     :multiple="false"
                                                     :trackBy="'id'"
                                                     :label="'name'"
                                                     :selectLabel="'Выбрать'"
                                                     :deselectLabel="'Убрать'"
                                                     :selectedLabel="'Выбрано'"
                                        >
                                            <template slot="option" slot-scope="props">
                                                <img v-show="props.option.image_url" class="option__image"
                                                     :src="props.option.image_url" alt="No Man’s Sky"/>
                                                <div class="option__desc">
                                                    <span class="option__title">{{ props.option.name }} (мест: {{props.option.seats_count}})</span>
                                                </div>
                                            </template>
                                            <span slot="noResult">По вашему запросу нет результатов.</span>
                                        </multiselect>
                                    </div>
                                </div>
                            </div>
                            <button class="btn btn_success" @click="addTable" :disabled="!additionalTable">Добавить</button>
                        </div>
                    </div>
                </div>
                <div class="form__row">
                    <button class="btn btn_primary" type="submit">Сохранить</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import Multiselect from 'vue-multiselect';
import Datepicker from "vue2-datepicker";
import {apiUrls} from "@/config/constants";
import handleFormValidationErrors from "@/assets/js/mixins/handleFormValidationErrors";

export default {
    name: 'TablePresetForDateForm',
    components: {
        Multiselect,
        Datepicker
    },
    mixins: [handleFormValidationErrors],
    props: {
        type: {
            type: String,
            default: 'create'
        },
        tablePreset: {
            type: Object,
            default: () => ({
                comment: '',
                id: '',
                name: '',
                restaurant_space_id: '',
                scale: 1,
                tables: []
            })
        }
    },
    data() {
        return {
            tablePresetForDateObj: {table_preset: {}, additional_tables: []},
            itemToSubmit: {},
            imageData: '',
            show: false,
            showPresetMenu: false,
            presetMenuX: 0,
            presetMenuY: 0,
            showTableMenu: null,
            tableMenuX: 0,
            tableMenuY: 0,
            showTableList: false,
            additionalTable: null,
            tables: [],
            movingTable: false,
            tableToMove: {pivot: {coord_x: 0, coord_y: 0}},
            prevX: 0,
            prevY: 0,
            deletingTable: false,
            tableToDelete: {},
            restaurantSpaces: [],
            tablePresets: [],
            datePreset: '',
        }
    },
    computed: {
        tableScale() {
            return parseFloat(this.tablePresetForDateObj.table_preset ? this.tablePresetForDateObj.table_preset.scale : this.tablePresetForDateObj.scale) * 5;
        }
    },
    methods: {
        getMargin(seats_count) {
            return -seats_count * this.tableScale / 2 > -30 ? -30 + 'px' : -seats_count * this.tableScale / 2 + 'px'
        },
        openPresetMenu(evt) {
            this.showTableMenu = false;
            let rect = evt.target.getBoundingClientRect();
            this.presetMenuX = Math.floor(evt.clientX - rect.left);
            this.presetMenuY = Math.floor(evt.clientY - rect.top);
            this.showPresetMenu = true;
        },
        openAddTable() {
            this.getTables();
            this.showTableList = true;
        },
        getTables() {
            this.$http.get('/available_tables_for_add_additional/' + this.tablePresetForDateObj.restaurant_space_id + '/' + this.tablePresetForDateObj.date)
                .then(resp => {
                    if (resp && resp.data && resp.data.data) {
                        let allTables = this.tablePresetForDateObj.table_preset.tables.concat(this.tablePresetForDateObj.additional_tables);
                        this.tables = resp.data.data.map(table => {
                            if (allTables.find(existingTable => existingTable.id === table.id)) {
                                table.$isDisabled = true
                            }

                            return table
                        });
                    }
                });
        },
        addTable() {
            this.additionalTable.pivot = {
                coord_x: this.presetMenuX,
                coord_y: this.presetMenuY
            };
            this.tablePresetForDateObj.additional_tables.push(this.additionalTable);
            this.showTableList = false;
        },
        openDeleteTable(table) {
            this.deletingTable = true;
            this.tableToDelete = table;
        },
        confirmDeleteTable() {
            this.tablePresetForDateObj.additional_tables = this.tablePresetForDateObj.additional_tables.filter(table => table.id !== this.tableToDelete.id);
            this.deletingTable = false;
        },
        openMoveTable(table) {
            this.showTableList = false;
            this.showTableMenu = false;
            this.tableToMove = table;
            this.prevX = table.pivot.coord_x;
            this.prevY = table.pivot.coord_y;
            this.movingTable = true;
            $(this.$refs.clickZone).on('click', event => {
                if (event.target === this.$refs.clickZone) this.moveTable(event);
            });
        },
        moveTable(evt) {
            let rect = evt.target.getBoundingClientRect();
            this.tableToMove.pivot.coord_x = Math.floor(evt.clientX - rect.left);
            this.tableToMove.pivot.coord_y  = Math.floor(evt.clientY - rect.top);
            this.$forceUpdate();
        },
        confirmTableMove() {
            $(this.$refs.clickZone).off('click');

            this.tablePresetForDateObj.additional_tables[this.tablePresetForDateObj.additional_tables.findIndex(table => table.id === this.tableToMove.id)] = this.tableToMove;
            this.tableToMove = {pivot: {}};
            this.movingTable = false;
        },
        cancelTableMove() {
            $(this.$refs.clickZone).off('click');

            this.tablePresetForDateObj.additional_tables[this.tablePresetForDateObj.additional_tables.findIndex(table => table.id === this.tableToMove.id)].pivot.coord_x = this.prevX;
            this.tablePresetForDateObj.additional_tables[this.tablePresetForDateObj.additional_tables.findIndex(table => table.id === this.tableToMove.id)].pivot.coord_y = this.prevY;
            this.tableToMove = {pivot: {}};
            this.movingTable = false;
        },
        openTableMenu(evt, table) {
            let $target = $(evt.target);
            if ($target.hasClass('coordinates') || $target.parents('.coordinates').length) return;

            this.showPresetMenu = false;
            let rect = evt.target.getBoundingClientRect();
            this.tableMenuX = Math.floor(evt.clientX - rect.left);
            this.tableMenuY = Math.floor(evt.clientY - rect.top);
            this.showTableMenu = table.id;
        },
        createItem() {
            return this.$http.post(apiUrls.tablesForDate, this.itemToSubmit)
                .then(data => data)
                .catch(error => {
                    this.handleFormValidationErrors(error, this.$refs.form)
                })
        },
        editItem() {
            return this.$http.put(apiUrls.tablesForDate + '/' + this.tablePresetForDateObj.id, this.itemToSubmit)
                .then(data => data)
                .catch(error => {
                    this.handleFormValidationErrors(error, this.$refs.form)
                })
        },
        async submitItem() {
            this.itemToSubmit = {
                date: this.tablePresetForDateObj.date,
                day_description: this.tablePresetForDateObj.day_description,
                additional_tables: this.tablePresetForDateObj.additional_tables.map(table => ({
                    id: table.id,
                    coord_x: table.pivot.coord_x,
                    coord_y: table.pivot.coord_y
                })),
                table_preset_id: this.tablePresetForDateObj.table_preset.id,
                restaurant_space_id: this.tablePresetForDateObj.restaurant_space_id
            };

            let resp;

            if (this.type === 'edit') {
                resp = await this.editItem();
            } else {
                resp = await this.createItem();
            }

            if (resp && resp.data && resp.data.success === true) {
                this.$emit('itemSubmitSuccess', this.itemToSubmit);
            }
        },
        getTablePresets() {
            this.$http.get(apiUrls.tablePreset).then(resp => {
                if (resp && resp.data && resp.data.data && resp.data.data.items) {
                    this.tablePresets = resp.data.data.items;
                }
            });
        }
    },
    mounted() {
        this.tablePresetForDateObj = Object.assign({}, this.tablePreset);

        $(window).on('click', evt => {
            let $target = $(evt.target),
                $parent = $(evt.target.parentElement);


            if (!$target.hasClass('preset') && !$target.parents('.preset').length) {
                this.showPresetMenu = false;
                this.showTableMenu = false;
                return;
            }

            if ($target.hasClass('additional') || $parent.hasClass('additional')) {
                this.showPresetMenu = false;
            } else {
                this.showTableMenu = false;
            }

        });

        this.getTablePresets();

        this.addWatcher('tablePresetForDateObj');
    },
    destroyed() {
        $(window).off('click');
    }
}
</script>